}

.menu-button .icon-cross {
  display: none;
}

.menu-button.is-open .icon-cross {
  display: block;
  scale: .5;
}

.menu-container.is-open .menu-button__icon {
  display: none;
}

.menu-button__icon {
  z-index: 5;
}

.menu__list {
  align-items: center;
