}

.menu__list-item {
  border: 2px solid #bfe55e;
  border-radius: 22px;
  padding: 9px 13px;
}

.menu-container.is-open {
  z-index: 2;
  height: 100vh;
  width: 100%;
  transition: opacity 650ms var(--cubic), visibility 250ms var(--cubic), transform 1000ms var(--cubic);
  background-color: #bfe55e;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 20px 10px 10px;
  font-size: 20px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0%);
}

.menu-container.is-open .menu__list {
  flex-direction: column;
  gap: 50px;
}

.menu-container.is-open .menu__link {
  text-transform: uppercase;
  color: #222;
  font-size: 18px;
  font-weight: 600;
  line-height: 22px;
}

.menu-container.is-open .menu__list-item {
  border: 2px solid #222;
  padding: 9px 13px;
}

@media screen and (max-width: 1246px) {
