  }
}

.header-holder {
  width: 100%;
  z-index: 4;
  background-color: #bfe55e;
  overflow: hidden;
}

.header-nav {
  display: none;
}

.header-nav.active {
  z-index: 10;
  opacity: 1;
  background-color: #bfe55e;
  transition: opacity .6s linear;
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.device-menu {
  z-index: 11;
  width: 45px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0);
  display: block;
  position: relative;
}

.device-menu .device-menu__inner {
  width: 24px;
  height: 18px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.device-menu span {
  width: 100%;
  height: 3px;
  background-color: #fff;
  position: absolute;
  top: 5px;
  left: 0;
}

.device-menu span:nth-child(1) {
  transition: rotate, all .3s;
  top: 0;
}

.device-menu span:nth-child(2) {
  top: 50%;
}

.device-menu span:nth-child(3) {
  transition: rotate, all .3s;
  top: 100%;
}

.device-menu.open span:nth-child(1) {
  top: 10px;
  right: 0;
  transform: rotate(45deg);
}

.device-menu.open span:nth-child(2) {
  opacity: 0;
}

.device-menu.open span:nth-child(3) {
  top: 10px;
  right: 0;
  transform: rotate(-45deg);
}

.device-menu.open .device-menu__inner span {
  background-color: #202020;
}

.device-menu.open .header-menu__logo {
  fill: #202020;
  height: 75px;
  padding: 0;
  display: block;
}

.menu__wrapper {
  width: 100%;
