  // ==========my styles==============

  .device-menu.open {

   .device-menu__inner span{
        background-color:#202020;
    }

    .header-menu__logo {
      display: block;
      fill: #202020;
      padding: 0;
      height: 75px;
  
    }
  }

  .menu__wrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-top: 30px;
  }

  .header-menu-link {
      font-family: 'Neue Montreal',sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;
      text-transform: uppercase;

      color: #202020; 
      padding: 9px 32px;
      border-radius: 22px;
      border: 3px solid #202020;   
  }
  
  .header-menu-list {
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }

  .menu__social-list {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
    padding: 20px 0;
    border-top: 2px solid #202020;
    

}
.menu__social-item {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: #202020;   
    cursor: pointer; 
}
  